.card {
  position: relative;

  padding: $space-md;

  background-color: $color-white;
  border: $border solid $color-border;
  border-radius: $border-radius;

  &__thumbnail {
    display: block;
    width: 100%;
    height: 0;
    padding-bottom: 41.6%;

    overflow: hidden;
    border-radius: $border-radius;
  }

  &__img {
    display: block;
    width: 100%;

    border-radius: $border-radius;
  }

  &__heading-link {
    text-decoration: none;

    @include hover {
      text-decoration: none;
    }
  }

  &__heading {
    font-size: $font-size-h4;
  }

  &__addon {
    position: absolute;
    top: $space-lg;
    right: $space-sm;

    padding: $space-xs $space-sm;

    font-size: $font-size-small;
    color: $color-white;

    background-color: $color-utility-neutral;
    border: $border solid $color-border;
    border-radius: $border-radius;
  }

  &--thin {
    padding: $space-sm;
  }
}
