// stylelint-disable plugin/no-unsupported-browser-features

.btn {
  display: inline-block;
  padding: $space-xs $space-md;

  line-height: $line-height-base;
  color: $color-text;
  text-align: center;
  white-space: nowrap;
  vertical-align: middle;

  cursor: pointer;
  background-color: $color-border;
  border-radius: $border-radius;

  @include border($color-border);

  &.active,
  &:hover {
    // color: currentColor;
    background-color: $color-white;
    outline: none;
  }

  &:focus {
    background-color: darken($color-border, 5%);
  }

  &:disabled {
    cursor: not-allowed;
    opacity: $opacity;

    &:hover {
      background-color: $color-border;
    }
  }

  &--large {
    padding: $space-md $space-lg;
  }

  &--small {
    padding: $space-xs $space-sm;

    font-size: $font-size-h6;
  }

  &--block {
    display: block;
    width: 100%;
  }

  &--text {
    padding: 0;
    background-color: transparent;
    border: 0;

    &:hover,
    &:focus {
      background-color: transparent;
      box-shadow: none;
    }
  }
}

// a.btn.active, a.btn:hover { /* stylelint-disable-line */
//   color: currentColor;
// }

/* Generate button options except special */
@each $type, $bg in (
  primary: $color-utility-neutral,
  // success: $color-success,
  // info: $color-info,
  // warning: $color-warning,
  // danger: $color-danger
) {
  .btn--#{$type} {
    color: $color-white;

    background-color: $bg;
    border-color: $bg;

    &.active,
    &:hover {
      color: $bg;
      background-color: $color-white;
    }

    &:focus {
      color: $color-white;

      background-color: $bg;
    }

    &:disabled {
      &:hover,
      &:focus {
        color: $color-white;

        background-color: $bg;
      }
    }
  }
}
