// stylelint-disable plugin/no-unsupported-browser-features

.nav-tab {
  display: flex;

  &__link {
    display: block;
    padding: $space-sm $space-md;

    font-weight: $font-weight-bold;
    color: $color-text;
    text-align: center;
    text-decoration: none;

    border-bottom: $border-med solid transparent;

    @include hover {
      color: $color-utility-neutral;
      text-decoration: none;

      border-bottom-color: $color-utility-neutral;
    }

    &.active {
      color: $color-utility-neutral;

      border-bottom-color: $color-utility-neutral;
    }
  }
}
