// stylelint-disable plugin/no-unsupported-browser-features

.layout-1 {
  display: flex;

  &__main {
    flex-grow: 1;
    order: 2;
    margin-top: $space-lg;
    margin-bottom: $space-lg;
  }

  &__main-inner {
    width: 100%;
    max-width: 600px;
    margin: 0 auto;
  }

  &__left,
  &__right {
    width: 260px;
  }

  &__left {
    order: 1;
    padding: $space-md;

    background-color: $color-white;
  }

  &__right {
    order: 3;
    padding-right: $space-sm;
    margin-top: $space-lg;
    margin-bottom: $space-lg;
  }
}
