// stylelint-disable plugin/no-unsupported-browser-features
.app-header {
  display: flex;
  align-items: center;

  &__logo {
    flex-shrink: 1;
  }

  &__menu {
    flex-grow: 1;
  }

  &__toggle {
    display: block;
    padding: $space-sm;

    color: $color-white;

    background-color: transparent;
    border: 0;
  }
}
