.nav {
  &__link {
    display: inline-block;
    padding: 0;

    font-weight: $font-weight-bold;
    color: $color-white;
    text-decoration: none;

    background-color: transparent;
    border: 0;

    &:not(:last-child) {
      margin-right: $space-md;
    }

    &.active {
      color: $color-gray-35;
    }

    @include hover {
      color: $color-gray-35;
      text-decoration: none;
    }
  }
}
