.modal {
  position: relative;

  &__overlay {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;

    background-color: rgba(0, 0, 0, 0.8);
  }

  &__content {
    position: relative;
    top: 50%;
    left: 50%;
    width: 100%;
    max-width: 600px;
    padding: $space-lg $space-lg $space-md;

    background-color: $color-white;
    border-radius: $border-radius;
    transform: translate3d(-50%, -50%, 0);
  }
}
